import { Entity } from './entity.js'
import { Game } from './game.js'
import { Genome } from './genome.js'
import { Organism } from './organism.js'

class Critters {
    /**
     * @param {*} emerga 
     */
    constructor(emerga) {}
    init() {
        var canvas = document.createElement("canvas");
        var ctx = canvas.getContext("2d");
        canvas.width = window.innerWidth;
        canvas.height = window.innerHeight;
        document.body.appendChild(canvas);

        var keysDown = {};
        addEventListener("keydown", function(e) {
            keysDown[e.keyCode] = true;
            if (e.keyCode == 13)
                game.renderStuff = !game.renderStuff;
        }, false);
    }
    run() {
        Game.prototype.numFood = 400;
        Game.prototype.numOrgs = 100;
        Game.prototype.renderStuff = true;

        Genome.prototype.maxSize = 16;
        Genome.prototype.minSize = 4;
        Genome.prototype.mutationFactor = 0.06;
        Genome.prototype.maxMutation = 0.12;
        Genome.prototype.minMutation = 0.003;
        Genome.prototype.sizeMutationMax = 1.3;
        Genome.prototype.colorMutationMax = 20;

        Organism.prototype = new Entity();
        Organism.prototype.constructor = Organism;
        Organism.prototype.width = 2;
        Organism.prototype.maxMaxHealth = 30;
        Organism.prototype.minMaxHealth = 7;
        Organism.prototype.healthmultiplier = 1;
        Organism.prototype.healthUpdateInterval = 60;
        Organism.prototype.reproductionInterval = 6 * 60;
        Organism.prototype.maxSpeed = 4;
        Organism.prototype.healthFromFood = 20;
        Organism.prototype.babyDistMin = 10;
        Organism.prototype.babyDistMax = 50;

        var game = new Game();
        var update = function() {
            game.update();
            window.setTimeout(update, 1000 / game.desiredFPS);
        }
        var render = function() {
            game.render();
            requestAnimFrame(render, canvas);
        }
        update();
        render();
    }
}

export { Critters };