const KolorWheel = require('../../lib/3rd-party/kolorwheel').KolorWheel;

const colors = ['#eaff7b', '#00ffab', '#29bdc1', '#d84242', '#913f92'];

const createColorPalette = () => {
    var h = -45,
        r = [];
    for (var n = 0; n < 10; n++) {
        var color = new KolorWheel([h, 100, 50]);
        r.push(color.getHex());
        h += 45;
    }
    return r;
};
const colorPalette = createColorPalette();

var cytostyle = [{
    selector: 'node',
    style: {
        'label': 'data(id)',
        'text-valign': 'center',
        'color': '#000000',
        'background-color': colorPalette[0]
    }
}, {
    selector: 'node[.node-highlighted]',
    style: {
        'color': '#000000',
        'background-color': '#abefca'
    }
}, {
    selector: 'edge',
    style: {
        'curve-style': 'bezier',
        'target-arrow-shape': 'triangle',
        'target-arrow-color': 'black',
        'width': 1,
        'line-color': '#000'
    }
}, {
    selector: '.edge-highlighted',
    style: {
        'width': 4,
        'line-color': '#abefca'
    }
}, {
    selector: 'node[name]',
    style: {
        'content': 'data(name)'
    }
}];
for (var i = 0; i < 10; i++) {
    cytostyle.push({
        selector: 'node[iteration=' + i + ']',
        style: {
            'background-color': colorPalette[i]
        }
    });
}
for (var i = 0; i < 10; i++) {
    cytostyle.push({
        selector: 'edge[iteration=' + i + ']',
        style: {
            'line-color': colorPalette[i],
            'target-arrow-color': colorPalette[i]
        }
    });
}

const ehStyle = [{
    selector: '.eh-handle',
    style: {
        'background-color': 'red',
        'width': 12,
        'height': 12,
        'shape': 'ellipse',
        'overlay-opacity': 0,
        'border-width': 12,
        'border-opacity': 0
    }
}, {
    selector: '.eh-hover',
    style: {
        'background-color': 'red'
    }
}, {
    selector: '.eh-source',
    style: {
        'border-width': 2,
        'border-color': 'red'
    }
}, {
    selector: '.eh-target',
    style: {
        'border-width': 2,
        'border-color': 'red'
    }
}, {
    selector: '.eh-preview, .eh-ghost-edge',
    style: {
        'background-color': 'red',
        'line-color': 'red',
        'target-arrow-color': 'red',
        'source-arrow-color': 'red'
    }
}, {
    selector: '.eh-ghost-edge.eh-preview-active',
    style: {
        'opacity': 0
    }
}];

export { cytostyle, ehStyle, colorPalette }