const { Graph, GraphNode } = require('./graph');

var singleton;
class WolframModelRules {
    constructor(machine) {
        this.machine = machine;
    }
    settingsUpdated() {
        this.machine.stop();
        this.machine.doEmitReset();
    }
}
class WolframModel {
    /**
     * 
     */
    constructor() {
        singleton = this;
        this._onCompleteIterations =
            this._graph =
            this._using =
            this._onIteration =
            this._replaceWith = null;
        this._targetIterations = this._remainingIterations = 0;

        this._rules = new WolframModelRules(this);
    }
    get rules() { return this._rules; }
    set rules(v) { this._rules = v; }
    get targetIterations() { return this._targetIterations; }
    set targetIterations(v) { this._targetIterations = v; }
    get remainingIterations() { return this._remainingIterations; }
    set remainingIterations(v) { this._remainingIterations = v; }
    get onIteration() { return this._onIteration; }
    set onIteration(v) { this._onIteration = v; }
    get onCompleteIterations() { return this._onCompleteIterations; }
    set onCompleteIterations(v) { this._onCompleteIterations = v; }
        /**
         * 
         * @param {*} els 
         */
    doIteration(els) {
            if (this.onIteration)
                this.onIteration(this, els);
        }
        /**
         * 
         * @param {*} els 
         */
    doCompleteIterations(els) {
            if (this.onCompleteIterations)
                this.onCompleteIterations(this, els);
        }
        /**
         * 
         * @param {*} graph 
         * @param {*} using 
         * @param {*} replaceWith 
         * @param {*} iterations 
         */
    transform(graph, using, replaceWith, iterations) {
        this._graph = graph;
        this._using = using;
        this._replaceWith = replaceWith;
        this._targetIterations = iterations;
        this._remainingIterations = iterations;
        var iterateSearchReplace = () => {
            if (this._remainingIterations == 0) {
                this.doCompleteIterations(this._graph);
                return;
            }
            this._graph.sum(this._replaceWith, 1);
            this._graph.dec(this._using);
            this._remainingIterations--;
            this.doIteration(this._graph);
            setTimeout(() => iterateSearchReplace(), 0);
        };
        Promise.resolve().then(iterateSearchReplace);
    }
}

module.exports = { WolframModel, Graph, GraphNode };