const samples = require('./scripts/samples.js');
const $ = require('jquery');
var singleton = null;

const dg = require('dat-gui');
const st = require("stats.js");

class Emerga {
    /**
     * 
     */
    constructor() {
        this.dat = dg;
        this.Stats = st;

        this.stats = [];
        this.guis = {};
    }
    static getInstance() {
            if (!singleton) {
                singleton = new Emerga();
            }
            return singleton;
        }
        /**
         * 
         */
    initCanvas(canvasId) {
            var canvas = document.getElementById(canvasId);
            if (canvas) {
                this.canvas = canvas;
                this.context = this.canvas.getContext("2d");
            }
            return this;
        }
        /**
         * 
         * @param {*} type 
         */
    initStats(type) {
            if (this.stats[type]) {
                return;
            }
            this.stats[type] = new this.Stats();
            this.stats[type].showPanel(type); // 0: fps, 1: ms, 2: mb, 3+: custom
            document.body.appendChild(this.stats[type].dom);
            return this;
        }
        /**
         * 
         * @param {*} type 
         */
    initGUI(type, etc) {
            if (this.guis[type]) {
                return;
            }
            this.guis[type] = new this.dat.GUI(etc);
            return this;
        }
        /**
         * 
         * @param {*} script 
         */
    run(script) {
        if (!script) {
            return;
        }
        var instance = new script(this);
        if (instance.init) {
            instance.init(this);
        }
        if (instance.run) {
            instance.run(this);
        }
        return this;
    }
}

var urlParams = new URLSearchParams(window.location.search);
var script = "WolframModel";
if (urlParams) {
    const params = urlParams.toString().split("&");
    params.forEach((param) => {
        const p = param.split("=");
        if (p[0] && (p[0] === "script" || p[0] === "s")) {
            script = p[1];
        }
    });
}
const emerga = Emerga
    .getInstance();

$(document).ready(
    () => emerga
    .run(samples[script])
);

module.exports = {
    Emerga: emerga
}