const StringIdGenerator = require('./stringid.js').StringIdGenerator;
const util = require('./util.js');

const FindFailedException = {};
const GraphDecException = {};

const isArr = Array.isArray;
const keys = Object.keys;
const assign = Object.assign;


const ValidationException = (e, m) => {
    this.getMessage = () => m;
}
const validate = (e, m) => {
    if (!e) throw ValidationException;
};

class Graph {
    /**
     * 
     */
    constructor(v) {
            this.graphNodes = [];
            this._graph = {};
            this._incr = 0;
            if (v) this.set(v);
        }
        /**
         * 
         */
    get graph() {
            return this._graph;
        }
        /**
         * 
         */
    set graph(v) {
            this._graph = v;
            this.rebuildArray();
        }
        /**
         * 
         */
    get array() {
            return this.toArray();
        }
        /**
         * 
         */
    set array(v) {
            validate(
                isArr(v) && v[0] instanceof GraphNode ||
                isArr(v) && isArr(v[0]) && v[0].length === 2,
                "input not an array of arrays");
            this.clear();
            v.forEach((el) => this.add(el));
            return this.toArray();
        }
        /**
         * 
         * @param {*} node 
         */
    add(node) {
            validate(node && ((node instanceof GraphNode) ||
                    isArr(node) && node.length === 2 ||
                    isArr(node) && isArr(node[0]) && node[0].length === 2),
                "must be a graphnode");
            if (
                isArr(node) && isArr(node[0]) && node[0].length === 2 ||
                isArr(node) && node[0] instanceof GraphNode
            ) return node.forEach((el) => this.add(el));
            if (isArr(node)) {
                var nme = node[0],
                    nto = node[1];
                node = new GraphNode();
                node.node = nme;
                node.to = nto;
            }
            const mkNod = () => ({ to: {} });
            var nodeInfo = this.graph[node.node];
            if (!nodeInfo) {
                nodeInfo = mkNod();
                this.graph[node.node] = nodeInfo;
            }
            nodeInfo.to[node.to] = nodeInfo.to[node.to] ?
                nodeInfo.to[node.to] + 1 : 1;
            this.graphNodes.push(node);
        }
        /**
         * 
         * @param {*} node 
         */
    get(node) {
            return this.graph[node];
        }
        /**
         * 
         * @param {*} v 
         */
    set(v) {
            validate(!v ||
                (v && keys(v)[0].to) ||
                (v && v instanceof Graph) ||
                v && isArr(v) && v[0] instanceof GraphNode ||
                v && isArr(v) && isArr(v[0]) && v[0].length === 2,
                "input not a graph or graph data");
            if (v && isArr(v) && v[0] instanceof GraphNode ||
                v && isArr(v) && isArr(v[0]) && v[0].length === 2)
                return v.forEach((el) => this.add(el));
            if (v) this.graph = v.graph;
        }
        /**
         * 
         */
    clear() {
            this.graph = {};
            this.graphNodes = [];
        }
        /**
         * 
         */
    toArray() {
            return this.graphNodes;
        }
        /**
         * 
         */
    toTOC() {
            var toc = {};
            this.graphNodes.forEach((p) => {
                toc[p.id] = toc[p.id] ? toc[p.id] + 1 : 1;
            });
            return toc;
        }
        /**
         * 
         */
    get nodes() {
            var els = {};
            this.array.forEach((el) => {
                els[el.node] = 1;
                els[el.to] = 1;
            });
            return keys(els);
        }
        /**
         * 
         * @param {*} g 
         */
    find(g) {
            const elsTOC = g.toTOC();
            const meTOC = this.toTOC();
            var foundCount = 0;
            try {
                keys(elsTOC).forEach((elsNodeKey) => {
                    const elsNode = elsTOC[elsNodeKey];
                    const meNode = meTOC[elsNodeKey];
                    if (!meNode || meNode < elsNode) {
                        throw FindFailedException;
                    }
                    var foundAmount = (meNode - elsNode) + 1;
                    foundCount = foundCount == 0 ? foundAmount : foundCount;
                    foundCount = foundCount > foundAmount ? foundAmount : foundCount;
                });
            } catch (e) {
                return 0;
            }
            return foundCount;
        }
        /**
         * 
         */
    rebuildArray() {
            var self = this;
            this.graphNodes = [];
            const meGraph = this.graph;
            keys(meGraph).forEach((nodeKey) => {
                const meNode = meGraph[nodeKey];
                if (isArr(meNode)) return;
                keys(meNode.to).forEach((toKey) => {
                    for (var i = 0; i < meNode.to[toKey]; i++) {
                        self.graphNodes.push(new GraphNode([nodeKey, toKey]));
                    }
                });
            });
        }
        /**
         * 
         * @param {*} g 
         * @param {*} times 
         */
    sum(g, times, deltas) {
            if (!times) times = 1;
            const elsGraph = assign({}, g.graph);
            const meGraph = assign({}, this.graph);

            var nodes = this.nodes
                .concat(g.nodes);
            nodes = nodes
                .filter((a, b) => nodes.indexOf(a) === b)
                .sort(util.sortAlphaNum);

            const an = new StringIdGenerator(nodes[nodes.length - 1]);

            for (var i = 0; i < times; i++) {
                var keyMap = {};
                keys(elsGraph).forEach((nodeKey) => {
                    var myNodeKey = nodeKey;
                    const elsNode = elsGraph[nodeKey];
                    var meNode = meGraph[nodeKey];
                    var meDeltaNode = meGraph[nodeKey];
                    if (!meNode) {
                        myNodeKey = keyMap[nodeKey];
                        if (!myNodeKey) {
                            myNodeKey = an.next();
                            keyMap[nodeKey] = myNodeKey;
                        }
                        meGraph[myNodeKey] =
                            meDeltaNode =
                            meNode =
                            elsNode;
                        return;
                    }
                    keys(elsNode.to).forEach((toKey) => {
                        var myToKey = toKey;
                        if (!meNode.to[toKey] && !meGraph[toKey]) {
                            myToKey = keyMap[toKey];
                            if (!myToKey) {
                                myToKey = an.next();
                                keyMap[toKey] = myToKey;
                            }
                        }
                        meDeltaNode.to[myToKey] = meNode.to[myToKey] = meNode.to[myToKey] ?
                            meNode.to[myToKey] + elsNode.to[toKey] :
                            elsNode.to[toKey];
                    });
                });
            }
            this.graph = meGraph;
        }
        /**
         * 
         * @param {*} g 
         */
    dec(g) {
            const elsGraph = g.array;
            const meGraph = this.array;

            const matrix = {},
                out = [];
            meGraph.forEach((el) => {
                if (!el.node || !el.to) return;
                const key = el.node + '_' + el.to;
                matrix[key] = matrix[key] !== undefined ? matrix[key] + 1 : 1;
            });
            elsGraph.forEach((el) => {
                if (!el.node || !el.to) return;
                const key = el.node + '_' + el.to;
                if (!matrix[key])
                    throw GraphDecException;
                matrix[key] = matrix[key] - 1;
                if (matrix[key] < 0)
                    throw GraphDecException;
            });
            keys(matrix).forEach((el) => {
                if (matrix[el] === 0)
                    delete matrix[el];
            })
            keys(matrix).forEach((el) => {
                var els = el.split('_');
                var cnt = matrix[el];
                for (var i = 0; i < cnt; i++)
                    out.push(new GraphNode(els));
            });
            this.array = out;
        }
        /**
         * 
         */
    toString() {
            const a = this.array;
            var acc = '';
            a.forEach((el) => {
                acc +=
                    el.id + ' ';
            });
            return acc;
        }
        /**
         * 
         */
    clone() {
        const g = new Graph();
        g.graph = assign({}, this.graph);
        return g;
    }
}
/**
 * 
 */
class GraphNode {
    /**
     * 
     * @param {*} n 
     */
    constructor(n) {
            this._node = [];
            if (!n) {
                return;
            }
            if (n instanceof GraphNode) {
                this.node = n.node;
                this.to = n.to;
            } else if (isArr(n) && n.length === 2) {
                this.node = n[0];
                this.to = n[1];
            } else validate(false, "bad input to constructor");
        }
        /**
         * 
         */
    get array() {
            return this._node;
        }
        /**
         * 
         */
    get id() {
            return '' + this._node[0] + '_' + this._node[1];
        }
        /**
         * 
         */
    get type() {
            return this.node.length > 1 ? 'connection' : 'node';
        }
        /**
         * 
         */
    get node() {
            return this._node[0];
        }
        /**
         * 
         */
    get to() {
            return this._node[1];
        }
        /**
         * 
         */
    set array(v) {
            validate(isArr(v) && v.length === 2,
                "input is not a graphnode array");
            this._node = v;
        }
        /**
         * 
         */
    set node(v) {
            this._node[0] = v;
        }
        /**
         * 
         */
    set to(v) {
        this._node[1] = v;
    }
}

export {
    GraphNode,
    Graph
}