var cy, app, iterations = 0,
    cytostylesEditor, preTransformGraph,
    cytostyle = cstyles.cytostyle,
    ehStyle = cstyles.ehStyle,
    cytostyleString = JSON.stringify(cytostyle, null, 4);
inputsChanged = false;

class RenderingSettings {
    constructor(app) {
        this.app = app;
        this._scaleFactor = 256;
    }
    get scaleFactor() {
        return this._scaleFactor;
    }
    set scaleFactor(v) {
        this._scaleFactor = v;
        this.app.settingsUpdated();
    }
}

/**
 * 
 */
function copyToClipboard(element) {
    var $temp = $("<input>");
    $("body").append($temp);
    $temp.val($(element).text()).select();
    document.execCommand("copy");
    $temp.remove();
}
/**
 * 
 * @param {*} g 
 */
const initGraph = (a) => {
    app = a;
    //
    var curCanv = document.getElementById("main");
    if (curCanv) curCanv.parentNode.removeChild(curCanv);
    // 
    $(dashboard).appendTo(body);
    $('#btn-advance').click(() => {
        preTransformGraph = new Graph();
        preTransformGraph.graph = assign({}, app.graph.graph);
        app.stateMachine.transform(app.graph, app.using, app.replaceWith, 1);
    });
    $('#btn-run').click(() => {
        preTransformGraph = new Graph();
        preTransformGraph.graph = assign({}, app.graph.graph);
        app.stateMachine.transform(app.graph, app.using, app.replaceWith, app.numIterations);
    });
    $('#btn-reset').click(() => {
        reset();
    });
    $('#num-iterations-input').change(() => {
        app.numIterations = $('#num-iterations-input').val();
    });
    $('.data-input').change((ev) => {
        if (updateChangedInputs()) {
            showGraph(false);
        }
    });
    $(window).resize(() => {
        resizeGraphArea();
    });
    cola.register(cytoscape.default);
    //
    document.addEventListener('DOMContentLoaded', () => {
        cytoscape.default.use(edgehandles);
    });
    //
    app.startWith = new Graph(sampleStartFind);
    app.graph = new Graph(samplereplaceWith);
    app.using = new Graph(sampleStartFind);
    app.replaceWith = new Graph(samplereplaceWith);

    app.stateMachine.onIteration = () => {
        iterations++;
    };
    $('#btn-reset').click(() => {
        copyToClipboard("#node-output");
    });
    $('#input-tab').click(() => {
        $('.tab-content #layout').hide();
        $('.tab-content #input').show();
    });
    $('#layout-tab').click(() => {
        $('.tab-content #layout').show();
        $('.tab-content #input').hide();
    });
    cytostylesEditor = ace.edit('cytostyle-input');
    cytostylesEditor.getSession().setMode('ace/mode/json');
    cytostylesEditor.setTheme('ace/theme/monokai');
    cytostylesEditor.on('blur', function() {
        var t = cytostylesEditor.getValue();
        cytostyleString = !cytostyleString ? '' : cytostyleString;
        if (cytostyleString && cytostyleString !== t) {
            try {
                cytostyle = JSON.parse(t);
                showGraph(false);
            } catch (e) {
                updateStatusLight('error');
                console.log(e);
            }
        }
    });
    //createConsole();
};

const reset = () => {
    nodeInfos = {};
    edgeInfos = {};
    graphnodes = [];
    graphedges = [];
    iterations = 0;
    preTransformGraph = undefined;
    app.graph = new Graph(samplereplaceWith);
    showGraph(false);
    updateStatusLight('success');
};

const resizeGraphArea = () => {
    var w = $('main').width();
    var h = $('main').height();
    $('#cy').width(w);
    //    $('#cy').height(h - 400);
}

/**
 * node-output
 * @param {*} g 
 */
const showGraph = (updated) => {
    _showGraph(updated);
};
/**
 * 
 */
const updateChangedInputs = () => {
    const buildFromArray = (g, arr) => {
        g.clear();
        g.add(arr);
    };
    if (!$('#start-with-input').val()) {
        return;
    }
    var start = '[' + $('#start-with-input').val() + ']';
    var searc = '[' + $('#search-for-input').val() + ']';
    var repla = '[' + $('#replace-with-input').val() + ']';
    try {
        start = JSON.parse(start);
        searc = JSON.parse(searc);
        repla = JSON.parse(repla);

        buildFromArray(app.startWith, start);
        buildFromArray(app.graph, start);
        buildFromArray(app.using, searc);
        buildFromArray(app.replaceWith, repla);

        cytostyle = JSON.parse(cytostylesEditor.getValue());
    } catch (e) {
        updateStatusLight('danger');
        return false;
    }
    inputsChanged = false;
    cytostyleString = cytostylesEditor.getValue();
    updateStatusLight('success');
    return true;
};
const updateStatusLight = (s) => {
    $('#quick-status')
        .html(s === 'success' ? 'Success' : 'Error')
        .removeClass('alert-' + (s === 'success' ? 'danger' : s))
        .addClass('alert-' + (s === 'success' ? s : 'danger'));
}

const updateCytoStyles = (s) => {
    cy.batch(function() {
        s.forEach((el) => {
            const sel = cy.$(el.selector);
            if (el.css) sel.css(el.css);
            else sel.style(el.style);
            sel.update();
        });
        cy.endBatch();
    });
}

var nodeInfos = {};
var edgeInfos = {};

var graphnodes = [];
var graphedges = [];

/**
 * 
 * @param {*} g 
 */
const _showGraph = (updated) => {
    resizeGraphArea();

    const buildGraphElements = () => {
        var addednodes = {},
            deltaGraph = app.graph.clone();

        if (preTransformGraph) {
            deltaGraph.dec(preTransformGraph);
        } else {
            nodeInfos = {};
            edgeInfos = {};
            graphnodes = [];
            graphedges = [];
        }
        const addGraphNode = (node) => {
            if (!nodeInfos[node]) {
                nodeInfos[node] = {
                    iteration: iterations
                }
            }
            graphnodes.push({
                data: {
                    id: node,
                    iteration: nodeInfos[node].iteration
                }
            });
        };
        const addGraphEdge = (node1, node2) => {
            var edgekey = iterations + '_' + node1 + '_' + node2;
            if (!edgeInfos[edgekey]) {
                edgeInfos[edgekey] = {
                    id: edgekey,
                    iteration: iterations
                }
            }
            graphedges.push({
                data: {
                    id: edgeInfos[edgekey].id,
                    source: node1,
                    target: node2,
                    iteration: edgeInfos[edgekey].iteration
                }
            });
        };
        deltaGraph.array.forEach((el) => {
            if (!el.node || !el.to)
                return;
            if (!addednodes[el.node]) {
                addGraphNode(el.node);
                addednodes[el.node] = el.node;
            }
            if (!addednodes[el.to]) {
                addGraphNode(el.to);
                addednodes[el.to] = el.to;
            }
            addGraphEdge(el.node, el.to);
        });
    };
    const cytoEls = buildGraphElements();

    const getGraphElementsString = (arr) => {
        var out = [];
        arr.forEach((e) => out.push([e.node, e.to]));
        var sw = JSON.stringify(out);
        return sw
            .replaceAt(sw.length - 1, ' ')
            .replaceAt(0, ' ')
            .trim();
    };

    const buildCytoElementsDisplayString = () => {
        if (!cy) return;

        const out = {
            nodes: [],
            edges: []
        };
        cy.nodes().forEach((el) => out.nodes.push(el.data()));
        cy.edges().forEach((ed) => out.edges.push(ed.data()));
        return jss(out, null, 2);
    }

    const displayOutputGraphData = () => {
        if (cy) {
            var nodes = cy.nodes();
            var edges = cy.edges();
            $('.num-iterations').html(iterations);
            $('#num-nodes').html(nodes.length);
            $('#num-edges').html(edges.length);
            $('#node-output').html(buildCytoElementsDisplayString());
        }
    }

    const updateInputElements = () => {
        $('#start-with-input').val(getGraphElementsString(app.graph.array));
        $('#search-for-input').val(getGraphElementsString(app.using.array));
        $('#replace-with-input').val(getGraphElementsString(app.replaceWith.array));
        cytostylesEditor.setValue(JSON.stringify(cytostyle, null, 4));
        $('#num-iterations-input').val(app.numIterations);
    };

    updateInputElements();
    displayOutputGraphData();

    cy = window.cy = cytoscape.default({
        container: document.getElementById('cy'),
        autounselectify: true,
        boxSelectionEnabled: false,
        layout: { name: 'cola' },
        style: cytostyle.concat(ehStyle),
        elements: {
            nodes: graphnodes,
            edges: graphedges
        }
    });
    // cy.cxtmenu({
    //     selector: 'node, edge',
    //     commands: [{
    //         content: '<span class="fa fa-flash fa-2x"></span>',
    //         select: function(ele) {
    //             console.log(ele.id());
    //         }
    //     }, {
    //         content: '<span class="fa fa-star fa-2x"></span>',
    //         select: function(ele) {
    //             console.log(ele.data('name'));
    //         },
    //         enabled: false
    //     }, {
    //         content: 'Text',
    //         select: function(ele) {
    //             console.log(ele.position());
    //         }
    //     }]
    // });
    // cy.cxtmenu({
    //     selector: 'core',
    //     commands: [{
    //         content: 'bg1',
    //         select: function() {
    //             console.log('bg1');
    //         }
    //     }, {
    //         content: 'bg2',
    //         select: function() {
    //             console.log('bg2');
    //         }
    //     }]
    // });
    cy.on("click", "node", function(event) {
        const me = event.target,
            hood = me.neighborhood(),
            c = me.style('background-color');

        hood.forEach((el) => el.data('color', el.style('background-color')));
        //
        me.animation({ style: { 'background-color': 'cyan' }, duration: 100 })
            .play() // start
            .promise('completed').then(function() { // on next completed
                me.animation({
                    style: { 'background-color': c },
                    duration: 100
                }).play();
                hood.forEach((el) => {
                    el.animation({
                        style: { 'background-color': el.data('color') },
                        duration: 100
                    }).play();
                });
            });
        //
        hood.forEach((el) => {
            el.animation({
                style: { 'background-color': 'cyan' },
                duration: 100
            }).play();
        });
    });

    cy.on("click", "edge", function(event) {
        const me = event.target,
            hood = me.nodes(),
            c = me.style('line-color');

        hood.forEach((el) => el.data('color', el.style('line-color')));

        me.animation({ style: { 'line-color': 'cyan' }, duration: 100 })
            .play() // start
            .promise('completed').then(function() { // on next completed
                me.animation({
                    style: { 'line-color': c },
                    duration: 100
                }).play();
                hood.forEach((el) => {
                    el.animation({
                        style: { 'line-color': el.data('color') },
                        duration: 100
                    }).play();
                });
            });
        hood.forEach((el) => {
            el.animation({
                style: { 'line-color': 'cyan' },
                duration: 100
            }).play();
        });
    });

};
