var cos = Math.cos,
    sin = Math.sin,
    sqrt = Math.sqrt,
    abs = Math.abs,
    atan2 = Math.atan2,
    sqr = function(v) { return v * v; },
    random = Math.random;

//-------------------------------------------------------
// Vectors, and not the kind you put stuff in
function Vector(x, y, z) {
    this.x = x || 0;
    this.y = y || 0;
    this.z = z || 0;
}
Vector.prototype = {
    add: function(vector) {
        this.x += vector.x;
        this.y += vector.y;
        this.z += vector.z;
        return this;
    },
    subtract: function(vector) {
        this.x -= vector.x;
        this.y -= vector.y;
        this.z -= vector.z;
        return this;
    },
    multiply: function(another) {
        this.x /= another.x;
        this.y /= another.y;
        this.z /= another.z;
        return this;
    },
    divide: function(another) {
        this.x /= another.x;
        this.y /= another.y;
        this.z /= another.z;
        return this;
    },
    scale: function(factor) {
        this.x *= factor;
        this.y *= factor;
        this.z *= factor;
        return this;
    },
    magnitude: function() {
        return sqrt(sqr(this.x + this.y));
    },
    distance: function(another) {
        return abs(sqrt(sqr(this.x - another.x) + sqr(this.y - another.y)));
    },
    angle: function(angle, magnitude) {
        if (angle && magnitude)
            return Vector.fromAngle(angle, magnitude);
        return atan2(this.y, this.x);
    },
    clone: function() {
        return new Vector(this.x, this.y, this.z);
    },
    equals: function(another) {
        return this.x === another.x &&
            this.y === another.y &&
            this.z === another.z;
    },
    random: function(r) {
        this.x += (random() * r * 2) - r;
        this.y += (random() * r * 2) - r;
        return this;
    }
};
Vector.fromAngle = function(angle, magnitude) {
    return new Vector(
        magnitude * cos(angle),
        magnitude * sin(angle),
        magnitude * sin(angle));
};

module.exports = { Vector }