class StringIdGenerator {
    constructor(start, chars = 'abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ') {
        if (!start) start = 'a';
        this._chars = chars;

        this._nextId = [];
        for (var i = 0; i < start.length; i++) {
            this._nextId.push(chars.indexOf(start.charAt(i)));
            this._increment();
        }
    }
    next() {
        const r = [];
        for (const char of this._nextId) {
            r.unshift(this._chars[char]);
        }
        this._increment();
        return r.join('');
    }
    _increment() {
            for (let i = 0; i < this._nextId.length; i++) {
                const val = ++this._nextId[i];
                if (val >= this._chars.length) {
                    this._nextId[i] = 0;
                } else {
                    return;
                }
            }
            this._nextId.push(0);
        }
        // *[Symbol.iterator]() {
        //     while (true) {
        //         yield this.next();
        //     }
        // }
}

module.exports = { StringIdGenerator }