var wolfram = require('../../lib/wolfram-model');
var singleton;
class InteractiveWolframModel {
    /**
     * 
     * @param {*} emerga 
     */
    constructor(emerga) {
            singleton = this;
            this._emerga = emerga;
            this.inited = false;
            this._numIterations = 1;
            this._iterationsPerformed = 0;
            this._ui = require('./ui');
            this._graph =
                this._startWith =
                this._using =
                this._replaceWith = null;
            this.setupStateMachine();
        }
        //
    get ui() { return this._ui; }
        //
    get numIterations() { return this._numIterations; }
    set numIterations(v) {
            this._numIterations = v;
            this.settingsUpdated();
        }
        //
    get iterationsPerformed() { return this._iterationsPerformed; }
    set iterationsPerformed(v) {
            this._iterationsPerformed = v;
            this.settingsUpdated();
        }
        // 
    get graph() { return this._graph; }
    set graph(v) {
            this._graph = v;
            this.settingsUpdated();
        }
        //
    get startWith() { return this._startWith; }
    set startWith(v) {
            this._startWith = v;
            this.settingsUpdated();
        }
        //
    get using() { return this._using; }
    set using(v) {
            this._using = v;
            this.settingsUpdated();
        }
        //
    get replaceWith() { return this._replaceWith; }
    set replaceWith(v) {
            this._replaceWith = v;
            this.settingsUpdated();
        }
        //
    get emerga() { return this._emerga; }
        /**
         * 
         * @param {*} machine 
         * @param {*} el 
         */
    iterationsCompleted(machine, el) {
            singleton.ui.showGraph(true);
        }
        /**
         * 
         * @param {*} machine 
         * @param {*} el 
         */
    iteration(machine, el) {
            this.iterationsPerformed = isNaN(this.iterationsPerformed) ? 1 : this._iterationsPerformed + 1;
        }
        /**
         * 
         */
    settingsUpdated() {}
        /**
         * 
         */
    transform(g, u, r, n) {
            app.stateMachine.transform(g, u, r, n);
        }
        /**
         * 
         */
    setupStateMachine() {
            var smRules = null;
            if (this.stateMachine) {
                smRules = this.stateMachine.rules;
            }
            this.stateMachine = new wolfram.WolframModel();
            if (smRules) {
                this.stateMachine.rules = smRules;
            }
            this.stateMachine.onCompleteIterations = this.iterationsCompleted;
        }
        /**
         * 
         */
    restart() {
            if (!singleton.inited) return;
            if (singleton.stateMachine) {
                singleton.setupStateMachine();
                singleton.ui.initCanvas(singleton);
                singleton.run();
            }
        }
        /**
         * 
         */
    init() {
            this.ui.initUI(this);
            this.inited = true;
            singleton.ui.showGraph(false);
        }
        /**
         * 
         */
    run() {
        this.inited = true;
        singleton.ui.showGraph(false);
    }
}
module.exports = InteractiveWolframModel;